body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.notify,.error {
  border: 5px solid green;
  border-radius: 5px;
  color: green;
  font: 2rem;
  margin: 1rem 0;
  padding: 1rem;
}
.error {
    border: 5px solid red;
    color: red;
}
.container {
  max-width: 1100px ;
  margin: auto;
}
.contact-info {
 display: flex;
 flex-direction: column;
 justify-content: center;
 align-items: center;
}
.contact-form {
  
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.contact-form div {
   
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}
.contact-name {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.contact-name button {
  background-color: rgb(99, 28, 28);
  color: #f3f3f3;
  border : none;
  border-radius: 2px;
  padding: .5rem 1rem;
  margin-left: 1rem;
}